<template>
  <div></div>
</template><template>
</template>

<script>
import api from '@/utils/api'
export default {
  name: 'Logout',
  mounted () {
    api.userLogout().then(res => {
      console.log(res)
      this.$store.dispatch('clearProfile')
      this.$router.replace({
        path: '/'
      })
    })
  }
}
</script>

<style scoped>

</style>



