<template>
  <div class="register">
    <Row>
      <Col :xs="{span: 20, offset:2}" :sm="{span: 16, offset:4}" :md="{span:12, offset:6}" :lg="{span: 8, offset:8}" >
        <div class="reg-container">
          <div class="reg-title">用户注册</div>
          <div class="form-container">
            <Form ref="formData" :model="formData" :rules="ruleRegister">
              <FormItem prop="username">
                <Input type="text" v-model="formData.username" placeholder="用户名">
                  <Icon type="ios-person-outline" slot="prepend"></Icon>
                </Input>
              </FormItem>
              <FormItem prop="password">
                <Input type="password" v-model="formData.password" placeholder="请输入密码">
                  <Icon type="ios-lock-outline" slot="prepend"></Icon>
                </Input>
              </FormItem>
              <FormItem prop="passwordAgain">
                <Input type="password" v-model="formData.passwordAgain" placeholder="请再一次输入密码">
                  <Icon type="ios-lock-outline" slot="prepend"></Icon>
                </Input>
              </FormItem>
              <FormItem prop="superUserId">
                <Input type="text" v-model="formData.superUserId" placeholder="请输入管理员QQ账号">
                  <Icon type="ios-pricetags-outline" slot="prepend"/>
                </Input>
              </FormItem>
            </Form>
            <div class="reg-button">
              <Button type="primary" style="width: 100%" @click="handleRegister">确认注册</Button>
            </div>
          </div>
        </div>
      </Col>
    </Row>
  </div>
</template>

<script>
import api from '@/utils/api'
export default {
  name: 'Register',
  data () {
    const checkUsernameExist = (rule, value, callback) => {
      api.checkUsername(value).then(res => {
        if (res.data.data.user === true) {
          callback(new Error('用户名重复'))
        } else {
          callback()
        }
      }, _ => callback())
    }
    const repeatUserPassword = (rule, value, callback) => {
      if (value !== this.formData.password) {
        callback(new Error('两次密码不一致'))
      }
      callback()
    }
    return {
      formData: {
        username: '',
        password: '',
        passwordAgain: '',
        superUserId: ''
      },
      ruleRegister: {
        username: [
          {required: true, trigger: 'blur'},
          {validator: checkUsernameExist, trigger: 'blur'}
        ],
        password: [
          {require: true, trigger: 'blur', min: 6, max: 20}
        ],
        passwordAgain: [
          {require: true, trigger: 'blur', min: 6, max: 20},
          {validator: repeatUserPassword, trigger: 'blur'}
        ],
        superUserId: [
          {required: true, trigger: 'blur', min: 5, max: 10}
        ]
      }
    }
  },
  methods: {
    handleRegister () {
      api.register(this.formData).then(res => {
        this.$Message.success('注册成功')
        this.$router.push('/login')
      }, _ => {
        console.log('Failed')
      })
    }
  }
}
</script>

<style scoped>
  .reg-container{
    background-color: rgba(255, 255, 255, 0.7);
    border-radius: 5px;
    margin-top: 50px;
    vertical-align: middle;
  }

  .reg-title{
    font-size: 20px;
    padding: 10px;
  }

  .form-container{
    padding: 15px;
  }
</style>



