<template>
  <div class div="container">
    <Modal title="添加用户" v-model="showModel" @on-ok="ok" @on-cancel="cancel()">
      <Form>
        <FormItem prop="number">
          <Input type="text" v-model="formData.userId" placeholder="QQ账号">
            <Icon type="ios-person-outline" slot="prepend"></Icon>
          </Input>
        </FormItem>
        <FormItem prop="count">
          <Input type="text" v-model="formData.count">
            <Icon type="ios-pricetags-outline" slot="prepend"/>
          </Input>
        </FormItem>
      </Form>
    </Modal>

    <div class = "select-container">
      <Row>
        <Col span="4" style="margin:10px">
          <Select v-model="command" filterable @on-change="commandChange">
            <Option v-for="item in commands" :value="item.patternString" :key="item.patternString">{{ item.patternString }}</Option>
          </Select>
        </Col>
        <Col span="4" style="margin:10px">
          <Select v-model="group" filterable @on-change="groupChange">
            <Option v-for="item in groups" :value="item.groupId" :key="item.groupId">{{ item.groupId }}</Option>
          </Select>
        </Col>
        <Col span="4" style="float: right; margin: 10px">
          <Button @click="showModel = !showModel">添加成员</Button>
        </Col>
      </Row>
    </div>

    <div class = "list-container">
      <Table border :columns="cols" :data="listData">
        <template slot-scope="{ row, index }" slot="count">
          <Input type="text" v-model="editCount" v-if="editIndex === index"></Input>
          <span v-else>{{ row.count }}</span>
        </template>

        <template slot-scope="{ row, index }" slot="action">
          <div v-if="editIndex === index">
            <Button @click="handleSave(index)">保存</Button>
            <Button @click="editIndex = -1">取消</Button>
          </div>
          <div v-else>
            <Button @click="handleEdit(row, index)">编辑</Button>
            <Button type="error" @click="confirm(row, index)">删除</Button>
          </div>
        </template>
      </Table>
    </div>
  </div>
</template>

<script>
import api from '@/utils/api'
export default {
  name: 'Group',
  data () {
    return {
      disabled: true,
      showModel: false,
      command: '',
      group: '',
      commands: [],
      groups: [],
      listData: [],
      editCount: 0,
      editIndex: -1,
      formData: {
        userId: '',
        count: ''
      },
      cols: [
        {
          title: '序号',
          key: 'id'
        },
        {
          title: '昵称',
          key: 'nickname'
        },
        {
          title: 'QQ号',
          key: 'userId'
        },
        {
          title: '是否在群',
          key: 'inGroup'
        },
        {
          title: '统计次数',
          slot: 'count'
        },
        {
          title: '操作',
          slot: 'action'
        }
      ]
    }
  },
  mounted () {
    api.getCommands(0, true).then(res => {
      this.commands = res.data.data
    })
  },
  methods: {
    commandChange (value) {
      api.getGroup(value).then(res => {
        this.groups = res.data.data
        this.listData = []
      })
    },
    groupChange (value) {
      api.getPerson(this.command, value).then(res => {
        this.listData = res.data.data
      })
    },
    handleSave (index) {
      api.editPerson(this.group, this.listData[index].id, this.editCount).then(res => {
        this.$Message.success('修改成功')
        this.editIndex = -1
        this.listData[index].count = this.editCount
      }, _ => {
        this.$Message.error('修改失败')
        this.editIndex = -1
      })
    },
    handleEdit (row, index) {
      this.editCount = row.count
      this.editIndex = index
    },
    ok () {
      api.addPerson(this.command, this.group, this.formData.userId, this.formData.count).then(res => {
        this.cancel()
        this.$Message.success('添加成功')
        this.groupChange(this.group)
      })
    },
    cancel () {
      this.formData.number = ''
      this.formData.count = ''
    },
    confirm (row, index) {
      this.$Modal.confirm({
        title: '确认删除',
        content: '<p>是否确认删除该条记录</p>',
        onOk: () => {
          api.deletePerson(this.listData[index].id).then(res => {
            this.$Message.success('删除成功')
            this.groupChange(this.group)
          })
        },
        onCancel: () => {
          this.$Message.error('已取消')
        }
      })
    }
  }
}
</script>

<style scoped>
  .select-container{
    margin: 10px;
    margin-left: 75px;
  }

  .list-container{
    width: 90%;
    margin: 0 auto;
  }
</style>



