<template>
  <div id="profile">
    <Row>
      <Col :xs="{span: 20, offset:2}" :sm="{span: 18, offset:4}" :md="{span:12, offset:6}" :lg="{span: 8, offset:8}" >
        <div class="profile-container">
          <div class="profileTitle">用户信息</div>
          <div class="form-container">
            <Form ref="userProfile" :model="userProfile">
              <FormItem prop="username">
                <Input disabled v-model="userProfile.username">
                  <Icon type="ios-person-outline" slot="prepend"></Icon>
                </Input>
              </FormItem>
              <FormItem prop="openSecret">
                <Input readonly v-model="userProfile.openSecret">
                  <Icon type="ios-key-outline" slot="prepend" />
                </Input>
              </FormItem>
              <FormItem prop="superUserId">
                <Input v-model="userProfile.superUserId">
                  <Icon type="ios-pricetags-outline" slot="prepend" />
                </Input>
              </FormItem>
              <FormItem prop="cqUrl">
                <Input v-model="userProfile.cqUrl">
                  <Icon type="ios-pricetags-outline" slot="prepend" />
                </Input>
              </FormItem>
            </Form>
            <div class="profileButton">
              <Button type="primary" style="width: 100%" @click="handleEditProfile">修改信息</Button>
            </div>
          </div>
        </div>
      </Col>
    </Row>
  </div>
</template>

<script>
import api from '@/utils/api'
export default {
  name: 'Profile',
  data () {
    return {
      userProfile: {
        username: '',
        openSecret: '',
        superUserId: '',
        cqUrl: ''
      }
    }
  },
  mounted () {
    api.getProfile().then(res => {
      let profile = res.data.data
      Object.keys(this.userProfile).forEach(element => {
        if (profile[element] !== undefined) {
          this.userProfile[element] = profile[element]
        }
      })
    })
  },
  methods: {
    handleEditProfile () {
      api.editProfile(this.userProfile).then(res => {
        this.$Message.success('修改成功')
      })
    }
  }
}
</script>

<style scoped>
  .profileTitle{
    font-size: 20px;
    padding: 15px;
  }

  .form-container{
    padding: 10px;
  }

  .profile-container{
    background-color: rgba(255, 255, 255, 0.7);
    border-radius: 5px;
    margin-top: 50px;
    vertical-align: middle;
  }
</style>



