import Vue from 'vue'
import Router from 'vue-router'
import Commands from '@/views/Commands'
import Group from '@/views/Group'
import Home from '@/views/Home'
import Login from '@/views/Login'
import Logout from '@/views/Logout'
import Profile from '@/views/Profile'
import Register from '@/views/Register'
import NotFount from '@/views/404'
import Portal from '@/views/Portal'
import storage from '@/utils/storage'

Vue.use(Router)

const router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      meta: {
        requiresAuth: false,
        title: '主页'
      }
    },
    {
      path: '/login',
      name: 'Login',
      component: Login,
      meta: {
        requiresAuth: false,
        title: '登录'
      }
    },
    {
      path: '/register',
      name: 'Register',
      component: Register,
      meta: {
        requiresAuth: false,
        title: '注册'
      }
    },
    {
      path: '/logout',
      name: 'Logout',
      component: Logout,
      meta: {
        requiresAuth: false,
        title: '登出'
      }
    },
    {
      path: '/profile',
      name: 'Profile',
      component: Profile,
      meta: {
        requiresAuth: true,
        title: '个人信息'
      }
    },
    {
      path: '/commands',
      name: 'Commands',
      component: Commands,
      meta: {
        requiresAuth: true,
        title: '命令管理'
      }
    },
    {
      path: '/group',
      name: 'Group',
      component: Group,
      meta: {
        requiresAuth: true,
        title: '群管理'
      }
    },
    {
      path: '/portal',
      name: 'Portal',
      component: Portal,
      meta: {
        requiresAuth: true,
        title: '教务处管理'
      }
    },
    {
      path: '/*',
      name: 'notFount',
      component: NotFount,
      meta: {
        requiresAuth: false,
        title: '页面丢失'
      }
    }
  ]
})

router.beforeEach((to, from, next) => {
  Vue.prototype.$Loading.start()
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (!storage.get('authed')) {
      Vue.prototype.$error('请先登录')
      next({
        name: 'home'
      })
    } else {
      console.log('test2')
      next()
    }
  } else {
    next()
  }
  next()

  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
})

router.afterEach((to, from, next) => {
  Vue.prototype.$Loading.finish()
})

export default router



