<template>
  <div class="login">
    <Row>
      <Col :xs="{span: 20, offset:2}" :sm="{span: 16, offset:4}" :md="{span:12, offset:6}" :lg="{span: 8, offset:8}" >
        <div class="login-container">
          <div class="login-title">用户登录</div>
          <div class="form-container">
            <Form ref="formData" :model="formData" :rules="ruleInline">
              <FormItem prop="username">
                <Input type="text" v-model="formData.username" placeholder="Username">
                  <Icon type="ios-person-outline" slot="prepend"></Icon>
                </Input>
              </FormItem>
              <FormItem prop="password">
                <Input type="password" v-model="formData.password" placeholder="Password">
                  <Icon type="ios-lock-outline" slot="prepend"></Icon>
                </Input>
              </FormItem>
            </Form>
            <div class="buttons">
              <Button type="primary" style="width: 100%" @click="handleLogin">登录</Button>
            </div>
          </div>
        </div>
      </Col>
    </Row>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import api from '@/utils/api'
import Vue from 'vue'
export default {
  name: 'Login',
  data () {
    return {
      formData: {
        username: '',
        password: ''
      },
      ruleInline: {
        username: [
          {
            required: true,
            message: 'Please fill in the user name',
            trigger: 'blur'
          }
        ],
        password: [
          { required: true, message: 'Please fill in the password.', trigger: 'blur' },
          { type: 'string', min: 6, message: 'The password length cannot be less than 6 bits', trigger: 'blur' }
        ]
      }
    }
  },
  // todo:添加登录页面检测功能，跳转
  methods: {
    ...mapActions(['getProfile']),
    handleLogin () {
      api.userLogin(this.formData.username, this.formData.password).then(res => {
        this.getProfile()
        let that = this
        setTimeout(
          function () {
            that.$router.push('/profile')
            Vue.prototype.$success('登录成功')
          }, 200)
      })
    }
  }
}
</script>

<style scoped>
  .login-container {
    background-color: rgba(255, 255, 255, 0.7);
    border-radius: 5px;
    margin-top: 50px;
  }

  .login-title {
    font-size: 20px;
    padding: 10px;
  }

  .form-container{
    padding: 15px;
  }
</style>



