<template>
  <div class="container">
    <div class="table-container">
      <Row>
        <Col :xs="{span: 22, offset:1}" :sm="{span: 18, offset:3}" :md="{span:20, offset:2}" :lg="{span: 22, offset:1}" >
          <Table border :columns="cols" :data="listData">
            <template slot-scope="{ row, index }" slot="action">
              <Button type="error" @click="confirm(row, index)">删除</Button>
            </template>
          </Table>
        </Col>
      </Row>
    </div>
    <div class="page-container">
      <Page :total="pageNum" @on-change="pageChange"></Page>
    </div>
  </div>
</template>

<script>
import api from '@/utils/api'
export default {
  name: 'Commands',
  data () {
    return {
      cols: [
        {
          title: '序号',
          key: 'id'
        },
        {
          title: '匹配字符串',
          key: 'patternString'
        },
        {
          title: '显示命令',
          key: 'outputCommand'
        },
        {
          title: '最大长度',
          key: 'maxLength'
        },
        {
          title: '称号',
          key: 'nickname'
        },
        {
          title: '是否正则匹配',
          key: 'openRe'
        },
        {
          title: '操作',
          slot: 'action'
        }
      ],
      listData: [],
      pageNum: 0,
      nowPage: 0
    }
  },
  mounted () {
    api.getCommandsNum().then(res => {
      this.pageNum = res.data.data.total_num
    })
    api.getCommands(1, false).then(res => {
      this.listData = res.data.data
    })
  },
  methods: {
    pageChange (index) {
      api.getCommands(index, false).then(res => {
        this.listData = res.data.data
      })
    },
    confirm (row, index) {
      this.$Modal.confirm({
        title: '确认删除',
        content: '<p>是否确认删除该条记录</p>',
        onOk: () => {
          api.deleteCommand(this.listData[index].id).then(res => {
            this.$Message.success('删除成功')
          })
        },
        onCancel: () => {
          this.$Message.error('已取消')
        }
      })
    }
  }
}
</script>

<style scoped>
  .table-container{
    padding: 5px;
  }
</style>



