<template>
  <div id="app">
    <Menu mode="horizontal" theme="dark">
      <MenuItem name="1" to="/">
        <Icon type="ios-paper"></Icon>
        首页
      </MenuItem>
      <MenuItem  name="2" to="/profile" v-if="isAuthenticated">
        <Icon type="ios-construct"></Icon>
        用户信息
      </MenuItem>
      <MenuItem name="3" to="/commands" v-if="isAuthenticated">
        <Icon type="ios-build" />
        命令管理
      </MenuItem>
      <MenuItem name="5" to="/group" v-if="isAuthenticated">
        <Icon type="ios-cloudy" />
        群管理
      </MenuItem>
      <MenuItem name="6" to="/portal" v-if="isAuthenticated">
        <Icon type="ios-school" />
        教务处管理
      </MenuItem>
      <MenuItem name="4" to="/logout" style="float: right" v-if="isAuthenticated">
        登出
      </MenuItem>
      <MenuItem  name="2" to="/login" style="float: right;" v-if="!isAuthenticated">
        登录
      </MenuItem>
      <MenuItem  name="3" to="/register" style="float: right;" v-if="!isAuthenticated">
        注册
      </MenuItem>
    </Menu>
  </div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'
export default {
  name: 'NavBar',
  computed: {
    ...mapGetters(['isAuthenticated'])
  },
  methods: {
    ...mapActions(['getProfile'])
  },
  mounted () {
    this.getProfile()
  }
}
</script>

<style scoped>

</style>



