import Vue from 'vue'
import axios from 'axios'
Vue.prototype.$http = axios
axios.defaults.baseURL = '/api'
axios.defaults.xsrfHeaderName = 'X-CSRFToken'
axios.defaults.xsrfCookieName = 'csrftoken'

export default {
  register (data) {
    return ajax('register', 'post', {
      data
    })
  },

  checkUsername (username) {
    return ajax('check_username', 'post', {
      data: {
        username
      }
    })
  },

  userLogin (username, password) {
    return ajax('login', 'post', {
      data: {
        username,
        password
      }
    })
  },

  userLogout () {
    return ajax('logout', 'post')
  },

  editProfile (data) {
    return ajax('profile', 'put', {
      data
    })
  },

  getProfile () {
    return ajax('profile', 'get')
  },

  getCommands (page, flag) {
    return ajax('commands', 'get', {
      params: {
        page,
        flag
      }
    })
  },

  deleteCommand (id) {
    return ajax('commands', 'delete', {
      params: {
        id
      }
    })
  },

  getCommandsNum () {
    return ajax('get_command_total', 'get')
  },

  getGroup (command) {
    return ajax('groups', 'get', {
      params: {
        command
      }
    })
  },

  getPerson (command, groupId) {
    return ajax('person', 'get', {
      params: {
        command,
        groupId
      }
    })
  },

  editPerson (groupId, id, count) {
    return ajax('person', 'put', {
      data: {
        groupId,
        id,
        count
      }
    })
  },

  addPerson (command, groupId, userId, count) {
    return ajax('person', 'post', {
      data: {
        command,
        groupId,
        userId,
        count
      }
    })
  },

  deletePerson (id) {
    return ajax('person', 'delete', {
      params: {
        id
      }
    })
  },

  getStuInfo () {
    return ajax('portal', 'get', {})
  },

  editStuInfo (stuId, stuPass) {
    return ajax('portal', 'put', {
      data: {
        stuId,
        stuPass
      }
    })
  }
}

function ajax (url, method, options) {
  if (options !== undefined) {
    var {params = {}, data = {}} = options
  } else {
    params = data = {}
  }
  return new Promise((resolve, reject) => {
    axios({
      url,
      method,
      params,
      data
    }).then(res => {
      resolve(res)
    }, res => {
      reject(res)
    })
  })
}



