<template>
  <div class="portal">
    <Row>
      <Col :xs="{span: 20, offset:2}" :sm="{span: 18, offset:4}" :md="{span:12, offset:6}" :lg="{span: 8, offset:8}" >
        <div class="portal-container">
          <div class="portalTitle">教务处信息</div>
          <div class="form-container">
            <Form ref="stuInfo" :model="stuInfo">
              <FormItem prop="lastQueryTime">
                <Alert show-icon>上一次任务执行时间：{{stuInfo.lastQueryTime}}</Alert>
              </FormItem>
              <FormItem prop="stuStatus">
                <Alert type="success" v-if="stuInfo.loginStatus" show-icon>已登录</Alert>
                <Alert type="error" v-if="!stuInfo.loginStatus" show-icon>登录失效</Alert>
              </FormItem>
            </Form>
            <div class="profileButton">
              <Button type="primary" style="width: 100%" @click="handleEditStuInfo">修改信息</Button>
            </div>
          </div>
        </div>
      </Col>
    </Row>
  </div>
</template>

<script>
import api from '@/utils/api'
export default {
  name: 'Portal',
  data () {
    return {
      stuInfo: {
        loginStatus: false,
        lastQueryTime: ''
      }
    }
  },
  mounted () {
    api.getStuInfo().then(res => {
      this.stuInfo = res.data.data
    })
  }
}
</script>

<style scoped>
  .portalTitle{
    font-size: 20px;
    padding: 15px;
  }

  .form-container{
    padding: 10px;
  }

  .portal-container{
    background-color: rgba(255, 255, 255, 0.7);
    border-radius: 5px;
    margin-top: 50px;
    vertical-align: middle;
  }
</style>



